const makeLFO = (context, frequency) => {
    const lfo = context.createOscillator();
    const lfoFrequency = lfo.frequency;
    lfo.frequency.value = frequency;
    return {
        lfo,
        lfoFrequency,
        input: lfo,
        output: lfo,
        changeFrequency: (newFrequency) => { lfoFrequency.value = newFrequency; },
        connect: (node) => {
            if ('input' in node)
                lfo.connect(node.input);
            else if (node instanceof AudioParam)
                lfo.connect(node);
            else
                lfo.connect(node);
        },
        start: () => { lfo.start(); }
    };
};
export default makeLFO;
