const makeOscillator = (context, n, frequency, type, oct) => {
    const oscillator = context.createOscillator();
    const wrapper = {
        oct,
        n,
        oscillator,
        frequency,
        type,
        input: oscillator,
        output: oscillator,
        start: () => {
            oscillator.type = type;
            oscillator.frequency.value = wrapper.frequency * Math.pow(2, oct - 1);
            oscillator.start();
        },
        changeType: (newType) => { oscillator.type = newType; },
        changeFrequency: (freq) => { oscillator.frequency.value = freq; },
        changeOctave: (octave) => {
            wrapper.frequency = wrapper.frequency * Math.pow(2, octave - 1);
        },
        connect: (node) => {
            if ('input' in node)
                oscillator.connect(node.input);
            else if (node instanceof AudioParam)
                oscillator.connect(node);
            else
                oscillator.connect(node);
        }
    };
    return wrapper;
};
export default makeOscillator;
