const makeAmp = (context, vol = 0.1) => {
    const gain = context.createGain();
    const amplitude = gain.gain;
    amplitude.value = vol;
    return {
        gain,
        input: gain,
        output: gain,
        amplitude,
        changeAmplitude: (v) => { amplitude.value = v; },
        connect: (node) => {
            if ('input' in node)
                gain.connect(node.input);
            else if (node instanceof AudioParam)
                gain.connect(node);
            else
                gain.connect(node);
        }
    };
};
export default makeAmp;
