const makeFilter = (context, cutoff = 22) => {
    const filter = context.createBiquadFilter();
    filter.type = 'lowshelf';
    filter.frequency.value = cutoff * 1000;
    filter.gain.value = 20;
    return {
        filter,
        frequency: filter.frequency,
        input: filter,
        output: filter,
        changeFilter: (newFreq) => { filter.frequency.value = newFreq; },
        connect: (node) => {
            if ('input' in node)
                filter.connect(node.input);
            else if (node instanceof AudioParam)
                filter.connect(node);
            else
                filter.connect(node);
        }
    };
};
export default makeFilter;
